@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css);
*{box-sizing:border-box}html{font-family:Arial, Helvetica, sans-serif;font-size:2.2vh;background:#E0E0E0}@media screen and (min-height: 720px) and (min-width: 812px){html{font-size:16px}}@media screen and (min-height: 850px) and (min-width: 812px){html{font-size:18px}}html,body,main,section{height:100%;width:100%}body{position:fixed;overflow:hidden}section{overflow-y:hidden}section.fadeOut{position:absolute}h1,h2,h3,p{margin:0}

a{color:#92DE17}.fadeInUp,.fadeOut{-webkit-animation-duration:.5s;animation-duration:.5s}.zoomIn,.zoomOut{-webkit-animation-duration:.25s;animation-duration:.25s}.wrapper{height:100%;width:100%;max-width:812px;margin:0 auto;padding:2rem;display:flex;flex-direction:column;justify-content:center;overflow:hidden}.wrapper article{margin-top:1rem;display:flex;flex:1 1;flex-direction:column;justify-content:space-between}

.intro{background:#000;color:#fff;padding:1rem 0}.intro article p{line-height:1.5}

header h1{font-size:2.5rem}header h1,header .subtitle{line-height:1;margin-bottom:0.4rem}header h1 span,header .subtitle span{display:block}header p{line-height:1}

.btn{-webkit-appearance:none;border:none;background:#fff;color:#000;font-family:inherit;font-size:inherit;font-weight:bold;padding:1rem 2rem;width:100%;border:0.2em solid #fff;outline:none;cursor:pointer;border-radius:.3em;transition:all .2s ease}@media (hover: hover){.btn:hover{background:#000;color:#fff}}.btn.btn-small{width:auto}.btn.btn-alt{background:#000;color:#fff;border-color:#000}@media (hover: hover){.btn.btn-alt:hover{background:#fff;color:#000;border-color:#000}}

footer{margin-top:2rem;text-align:center;font-size:0.8rem}footer p{margin-top:1rem;line-height:1}

.loader{position:absolute;bottom:0;left:0;right:0;height:0.3rem;background:rgba(240,240,240,0.3);-webkit-animation:fade 1s ease infinite;animation:fade 1s ease infinite;pointer-events:none;transition:all .5s ease}.loader.hidden{-webkit-animation:fadeOut 1s ease forwards;animation:fadeOut 1s ease forwards}@-webkit-keyframes fade{0%,100%{background:rgba(240,240,240,0.3)}50%{background:rgba(240,240,240,0.2)}}@keyframes fade{0%,100%{background:rgba(240,240,240,0.3)}50%{background:rgba(240,240,240,0.2)}}

.quiz{background:#E0E0E0;color:#000}.quiz header{display:flex;justify-content:space-between}.quiz header>div .number{font-size:1.8rem;font-weight:900}.quiz header>div .number span{font-size:0.7em}.quiz header>div:last-of-type{text-align:right}.quiz ul{list-style:none;margin:0 -2rem -2rem;padding:0 2rem 2rem;white-space:nowrap;width:calc(100% + 4rem);overflow-y:hidden;overflow-x:auto;font-size:0.9rem;-webkit-overflow-scrolling:touch}.quiz ul li{margin-right:.75rem}.quiz ul li button{-webkit-appearance:none;background:none;border:none;outline:none;font-weight:inherit;text-align:inherit}.quiz ul li:last-of-type{margin-right:0}@media screen and (min-width: 812px){.quiz article{justify-content:center}.quiz ul{flex:.7 1;display:flex;justify-content:center;margin-bottom:0;padding:2rem 0}.quiz ul li{margin:0 0.5rem}}

.card{display:inline-block;width:11em;height:15em;background:#fff;font-weight:bold;white-space:normal;border-radius:.4em;overflow:hidden;position:relative;cursor:pointer;border:2px solid #fff;transition:all .2s ease}.card>span,.card>button{display:block;height:100%;width:100%;padding:1em;cursor:inherit;position:relative}.card>span span,.card>button span{position:absolute;top:0;left:0;padding:inherit}.card::before{content:"";position:absolute;bottom:1.2rem;left:1rem;font-size:0.5em;width:.7em;height:1em;background:#92DE17;border-radius:.1em;transform:rotate(-10deg)}.card::after{content:"Quiz Against Humanity";position:absolute;bottom:1.2rem;left:calc(1rem + 1.2em);font-size:.5em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}.card.card-question{margin:0 auto;background:#000;color:#fff;cursor:default;font-size:1.1rem;border-color:#000}@media (hover: hover){.card:not(.card-question):focus-within{border-color:#000}.card:not(.card-question):hover{font-size:1.05em}}

.overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(224,224,224,0.75);display:flex;align-items:center;justify-content:center;-webkit-animation-duration:.5s;animation-duration:.5s}.overlay article{background:#fff;color:#000;width:90%;max-width:600px;padding:2rem;border-radius:.3em;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.overlay article h2{margin-bottom:1rem}.overlay article p{line-height:1.5;margin-bottom:2rem}.overlay article p span{font-weight:bold}.overlay article p span.correct{color:#74b012}.overlay article p span.wrong{color:#cd5c5c}

.results{background:#000;color:#fff;padding:1rem 0}.results article div{text-align:center}.results article p{line-height:1.5}.results .description{margin:0 auto;width:100%;max-width:400px}.results .score{line-height:1;margin:0;font-size:5rem;font-weight:900;margin-bottom:.5rem}.results .score span{font-size:0.5em;font-weight:bold}.results .nothing{-webkit-animation:nothing 1s ease;animation:nothing 1s ease}@-webkit-keyframes nothing{0%{opacity:1}100%{opacity:0.999}}@keyframes nothing{0%{opacity:1}100%{opacity:0.999}}

