@import '../../App/colors';

.results {
  background: $bg;
  color: $fg;
  padding: 1rem 0;

  article {
    div {
      text-align: center; }

    p {
      line-height: 1.5; } }

  .description {
    margin: 0 auto;
    width: 100%;
    max-width: 400px; }

  .score {
    line-height: 1;
    margin: 0;
    font-size: 5rem;
    font-weight: 900;
    margin-bottom: .5rem;

    span {
      font-size: 0.5em;
      font-weight: bold; } }

  .nothing {
    animation: nothing 1s ease; } }

@keyframes nothing {
  0% {
    opacity: 1; }
  100% {
    opacity: 0.999; } }
