@import './App/colors';

@import url('https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css');

* {
  box-sizing: border-box; }

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2.2vh;
  background: $bg_alt;

  @media screen and (min-height: 720px) and (min-width: 812px) {
    font-size: 16px; }
  @media screen and (min-height: 850px) and (min-width: 812px) {
    font-size: 18px; } }

html, body, main, section {
  height: 100%;
  width: 100%; }

body {
  position: fixed;
  overflow: hidden; }

section {
  overflow-y: hidden;

  &.fadeOut {
    position: absolute; } }

h1, h2, h3, p {
  margin: 0; }
